
import util from '@/libs/util'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'

import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'


export default{
mixins: [ common, globals ],
components: {
Flexbox,
FlexboxItem
},
data () {
return {
data: [],
map_valid: {0:'待确认',1:'有效',2:'无效'}
}
},
computed: {
data_uri () {
return util.sprintf('market_clues?recommend_sid=%s',this.sid$)
}
},
methods: {
track_status(item){
if(item.is_reward == 1){
return '已奖励'
}else if(item.is_deal == 1){
return '已成交，待发放奖励'
}else if(item.cu_id > 0){
return '跟单中'
}else{
return '待跟单'
}
}
}
}




